.Auth-card{
    margin-top: 0rem;
    background-color: rgb(236, 226, 226);
    position: sticky;
    bottom: 0%;
    width: 100%;
    height: 40vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow-y: auto;
}
.Auth-body{
    margin: 4px;
    width: 99%;
    box-shadow: -2px 5px 5px gray;
    height: 30vh;
    border: 2px solid gray ;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    overflow-y: auto;
}
.Auth-title{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    overflow-y: auto;
}
.Auth-input{
    width: 60%;
    height: 2rem;
    margin-top: 3rem;
    margin-left: 7rem;
    border-radius: 6px;
    border: 2px solid ;
}
.Auth-submit{
    background-color: rgb(2, 173, 77);
    width: 80%;
    height: 3rem;
    margin-top: 1.5rem;
    margin-left: 7rem;
    border-radius: 10px;
    border: none;
    color: white;
    font-size: 1rem;
}
.Auth-otp{
    
        width: 10%;
        height: 2rem;
        margin-top: 3rem;
        margin-left: 7rem;
        border-radius: 6px;
        border: 2px solid ;
        text-align: center;
}
.input-otp{
    display: flex;
    width: 98%;
}
